<template>
    <div class="header">
        <div class="content-wraper">
            <div class="avatar">
                <img width="64" height="64" :src="seller.avatar" alt="">
            </div>
            <div class="content">
                <div class="title">
                    <span class="brand"></span>
                    <span class="name">{{seller.name}}</span>
                </div>
                <div class="description">
                    {{seller.description}}/{{seller.deliveryTime}}分钟送达
                </div>

                <!-- 判断数据是否存在,初始化时为undefined -->
                <div v-if="seller.supports" class="support">
                    <!-- 通过该数组切换不同的图片,利用stylus定义不同的图片变量 -->
                    <span class="icon" :class="classMap[seller.supports[0].type]"></span>
                    <span class="text">{{seller.supports[0].description}}</span>
                </div>
            </div>
            <div v-if="seller.supports" class="support-count" @click="showDetail">
                <span class="count">{{seller.supports.length}}个</span>
                <i class="icon-keyboard_arrow_right"></i>
            </div>
        </div>
        <div class="bulletin-wraper">
            <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
            <i class="icon-keyboard_arrow_right"></i>
        </div>
        <div class="background">
            <img width="100%" height="100%" :src="seller.avatar" alt="">
        </div>
       <transition name="fade">
        <div v-show="detailShow" class="detail">
            <div class="detail-wrapper clearfix">
                <div class="detail-main">
                    <h1 class="name">{{seller.name}}</h1>
                    <div class="star-wrapper">
                        <star :size="48" :score="seller.score"></star>
                    </div>
                    <div class="title">
                        <div class="line"></div>
                        <div class="text">优惠信息</div>
                        <div class="line"></div>
                    </div>
                    <ul v-if="seller.supports" class="supports">
                        <li class="support-item" v-for="(item, $index) in seller.supports">
                            <span class="icon" :class="classMap[seller.supports[$index].type]"></span>
                            <span class="text">{{seller.supports[$index].description}}</span>
                        </li>
                    </ul>
                    <div class="title">
                        <div class="line"></div>
                        <div class="text">商家公告</div>
                        <div class="line"></div>
                    </div>
                    <div class="bulletin">
                        <p class="content">{{seller.bulletin}}</p>
                    </div>
                </div>
            </div>
            <div class="detail-close">
                <i class="icon-close" @click="hideDetail"></i>
            </div>
        </div>
       </transition>
    </div>
</template>

<script>
import star from "../star/star";

export default {
    props: {
        seller: {
            type: Object
        }
    },

    data() {
        return {
            detailShow: false
        }
    },

    methods: {
        showDetail() {
            this.detailShow = true;
        },

        hideDetail() {
            this.detailShow = false;
        }
    },

    created() {
        this.classMap = ['decrease', 'discount', 'invoice', 'special', 'guarantee']
    },

    components: {
        star
    }
};
</script>

<!--知识点: 1.display: inline-block 消除间隔,父级元素使用 font-size: 0-->
<style lang="stylus" rel="stylesheet/stylus" scoped>
    .header
        position: relative
        color: #fff
        background: rgba(7, 17, 27, .5)
        overflow: hidden
        .content-wraper
            position: relative
            padding: 24px 12px 18px 24px;
            font-size: 0
            .avatar 
                display: inline-block
                vertical-align: top
                img 
                    border-radius: 2px
            .content
                display: inline-block
                font-size: 14px
                margin-left: 16px
            .title
                margin: 2px 0 8px 0;
                .brand 
                    display: inline-block
                    width: 30px
                    height: 18px
                    bg-image('brand')
                    background-size: 30px 18px
                    background-repeat: no-repeat
                    vertical-align: top
                .name
                    font-size: 16px
                    margin-left: 6px
                    line-height: 18px
                    font-weight: bold
            .description
                margin-bottom: 10px
                font-size: 12px
                line-height: 12px
            .support
                .icon
                    display: inline-block
                    width: 12px
                    height: 12px
                    margin-right: 4px
                    background-size: 12px 12px
                    background-repeat: no-repeat
                    vertical-align: middle
                    &.decrease
                        bg-image('decrease_1')
                    &.discount
                        bg-image('discount_1')
                    &.guarantee
                        bg-image('guarantee_1')
                    &.invoice
                        bg-image('invoice_1')
                    &.special
                        bg-image('special_1')
                .text
                    line-height: 12px
                    font-size: 12px
            .support-count
                position: absolute
                right: 12px
                bottom: 16px
                padding: 0 8px
                height: 24px
                line-height: 24px
                border-radius: 14px
                background: rgba(0, 0, 0, .2)
                text-align: center
                .count 
                    font-size: 10px
                    vertical-align: top
                .icon-keyboard_arrow_right
                    font-size: 10px
                    line-height: 24px
                    margin-left: 2px
        .bulletin-wraper
            font-size: 0
            height: 28px
            line-height: 28px
            padding: 0 22px 0 12px;
            white-space: nowrap 
            text-overflow: ellipsis
            overflow: hidden
            background: rgba(7, 17, 27, .2)
            position: relative
            .bulletin-title
                display: inline-block
                width: 22px
                height 12px
                bg-image('bulletin')
                background-size: 22px 12px
                background-position: no-repeat
                vertical-align: top
                margin-top: 8px
            .bulletin-text
                vertical-align: top
                font-size: 10px
                color: rgb(255, 255, 255)
                font-weight: 200
                margin: 0 4px
            .icon-keyboard_arrow_right
                // margin-left: 4px
                position: absolute 
                font-size: 10px
                right: 12px
                top: 8px
        .background
            position: absolute
            top: 0
            left: 0
            width: 100%
            height: 100%
            z-index: -1
            filter: blur(10px)
        .detail
            position: fixed
            z-index: 100
            width: 100%
            height: 100%
            overflow: auto 
            top: 0
            left: 0
            backdrop-filter: blur(10px)
            background: rgba(7, 17, 27, .8)
            &.fade-enter-active,
            &.fade-leave-active{
                transition: all .5s
            }
            &.fade-enter
                opacity: 1
                background: rgba(7, 17, 27, .8)
            &.fade-leave-active, &.fade-enter
                opacity: 0
                background: rgba(7, 17, 27, .8)
            .detail-wrapper
                width: 100%
                min-height: 100%
                .detail-main
                    margin-top: 64px
                    padding-bottom: 64px
                    .name
                        font-size: 16px
                        font-weight: 700
                        text-align: center
                        color: rgb(255, 255, 255)
                        line-height: 16px
                    .star-wrapper
                        margin-top: 18px
                        padding: 2px 0
                        text-align: center
                    .title
                        display: flex
                        width: 80%
                        margin: 28px auto 24px auto 
                        .line
                            flex: 1
                            position: relative
                            top: -6px
                            border-bottom: 1px solid rgba(255, 255, 255, .2)
                        .text
                            padding: 0 12px
                            font-size: 14px
                            font-weight: 700
                    .supports
                        width: 80%
                        margin: 0 auto 
                        .support-item
                            padding: 0 12px
                            margin-bottom: 12px
                            font-size: 0
                            &:last-child
                                margin-bottom: 0
                            .icon 
                                display: inline-block
                                width: 16px
                                height: 16px
                                vertical-align: top
                                margin-right: 6px
                                background-size: 16px 16px
                                background-position: no-repeat
                                &.decrease
                                    bg-image('decrease_2')
                                &.discount
                                    bg-image('discount_2')
                                &.guarantee
                                    bg-image('guarantee_2')
                                &.invoice
                                    bg-image('invoice_2')
                                &.special
                                    bg-image('special_2')
                            .text
                                line-height: 16px
                                font-size: 12px
                    .bulletin
                        width: 80%
                        margin: 0 auto 
                        .content
                            padding: 0 12px
                            line-height: 24px
                            font-size: 12px
            .detail-close
                position: relative
                width: 32px
                height: 32px
                margin: -64px auto 0 auto
                clear: both
                font-size: 32px
</style>



