<template>
    <div class="ratingselect">
        <div class="rating-type">
            <span @click="select(2, $event)" class="block positive" :class="{'active': selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
            <span @click="select(0, $event)" class="block positive" :class="{'active': selectType===0}">{{desc.positive}}<span class="count">{{positive.length}}</span></span>
            <span @click="select(1, $event)" class="block negative" :class="{'active': selectType===1}">{{desc.negative}}<span class="count">{{negative.length}}</span></span>
        </div>
        <div @click="toggleContent" class="switch" :class="{on: onlyContent}">
            <span class="icon-check_circle"></span>
            <span class="text">只看有内容的评价</span>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
// all: '全部',
// positive: '推荐',
// negative: '吐槽'
const POSITIVE = 0;
const NEGATIVE = 1;
const ALL = 2;

export default {
    props: {
        //传入ratings数组,跟food.ratings关联
        ratings: {
            type: Array,
            default() {
                return [];
            }
        },
       //跟selectType关联,通过在父组件里面设置这3个值来实现控制子组件的操作
        selectType: {
            type: Number,
            default: ALL
        },
        //跟onlyContent关联
        onlyContent: {
            type: Boolean,
            default: true
        },
        //跟desc关联
        desc: {
            type: Object,
            default() {
                return {
                    all: '全部',
                    positive: '满意',
                    negative: '不满意'
                }
            }
        }
    },
    computed: {
        positive() {
            return this.ratings.filter((rating) => {
                return rating.rateType === POSITIVE;
            })
        },
        negative() {
            return this.ratings.filter((rating) => {
                return rating.rateType === NEGATIVE;
            });
        }
    },
    methods: {
        select(type, event) {
            if (!event._constructed) {
                return;
            }
            // this.selectType = type;
            this.$emit('select', type);  //调用父组件的方法
        },
        toggleContent(event) {
            if (!event._constructed) {
                return;
            }
            // this.onlyContent = !this.onlyContent;
            this.$emit('toggle');
        }
    }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
    .ratingselect
        .rating-type
            padding: 18px 0
            margin: 0 18px
            border-1px(rgba(7, 17, 27, .1))
            font-size: 0
            .block
                font-size: 12px
                display: inline-block
                line-height: 16px
                padding: 8px 12px
                margin-right: 8px
                border-radius: 1px
                color: rgb(77, 85, 93)
                &.active
                    color: rgb(255, 255, 255)
                .count
                    font-size: 8px
                    margin-left: 2px
                &.positive
                    background: rgba(0, 160, 220, .2)
                    &.active
                        background: rgb(0, 160, 220)
                &.negative
                    background: rgba(77, 85, 93, .2)
                    &.active
                        background: rgb(77, 85, 93)
        .switch
            padding: 12px 18px
            line-height: 24px
            border-bottom: 1px solid rgba(7, 17, 27, .1)
            color: rgb(147, 153, 159)
            font-size: 0
            &.on
                .icon-check_circle
                    color: #00c850
            .icon-check_circle
                display: inline-block
                vertical-align: top
                margin-right: 4px
                font-size: 24px
            .text
                vertical-align: top
                font-size: 12px
</style>





